#searchForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#searchLabel {
    color: #ECECED;
}

#searchBar {
    border-radius: 1rem .5rem 1rem .25rem ;
    border: 4px solid #050B11;
    width: 20rem;
    padding: .375rem;

    &:focus-visible {
        outline: none;
    }
}

#resultList {
    position: absolute;
    left: 25%;
    right: 25%;
    margin: 0 auto;
    display: flex;
    flex-flow: column nowrap;
    width: 20%;
    border-radius: .25rem;
    background-color: #191914bf;
}

.songResult {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-around;

    margin: .5% 1%;
    border: solid .03125rem #191414;
    border-radius: .5rem;
    background-color: #b1b1b13b;
    color: rgb(238, 216, 238);
}

.songContainer p {
    border: .8px solid #ffffff00;
}

.artist {
    font-size: 0.75rem;
}

.album {
    font-size: 0.625rem;
}

@media only screen and (max-width: 670px) {
    #searchBar {
        width: min(23rem, 75dvw);
    }
}